  max-width: 200px;
  max-height: 200px;
  position: fixed;
  top: 50px;
  right: 30%;
}

.wrap-pokemons {
  padding-bottom: 100px;
}

.show-pokemons {
  border-radius: 50px;
  margin: 50px auto 0;
  padding: 20px;
  display: block;
}

.pokemon-name {
  color: #000;
  text-shadow: 1px 1px 2px pink;
  text-transform: uppercase;
  background-color: #ffe952;
  border-radius: 50px;
  padding: 15px;
  font-weight: bold;
}

